import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
import imgUrl from "../../assets/background1.jpg";
import {
  Button,
  Container,
  InputGroup,
  InputGroupAddon,
  Input,
  Card,
  Row,
  Col,
  Form,
  CardHeader
} from "reactstrap";
import Navbar from "../../components/Navbar";

export default class Register extends React.Component {
  state = {
    usuario: {
      nombre: "",
      apellido: "",
      telefono: "",
      correo: "",
      psw: "",
      psw2: ""
    }
  };
  handleChange = event => {
    this.setState({
      usuario: {
        ...this.state.usuario,
        [event.target.name]: event.target.value
      }
    });
  };
  render() {
    return (
      <header id="home">
        <div
          className="bg-img"
          style={{ backgroundImage: "url(" + imgUrl + ")" }}
        >
          <div className="overlay" />
        </div>
        <div className="home-wrapper">
          <Container className="mt-0">
            <Row>
              <Col sm={{ size: 7 }}>
                <h1 className="white-text">Mapa del Conocimiento</h1>
                <p className="white-text">
                  {" "}
                  Registrate para compartir tus investigaciones y acceder al
                  conocimiento con todos
                </p>
              </Col>
              <Col sm={{ size: 5 }}>
                <Card body>
                  <CardHeader>Crear Cuenta</CardHeader>

                  <Form>
                    <InputGroup className="my-2">
                      <InputGroupAddon addonType="prepend">N</InputGroupAddon>
                      <Input placeholder="Nombre" type="text" />
                    </InputGroup>
                    <InputGroup className="my-3">
                      <InputGroupAddon addonType="prepend">A</InputGroupAddon>
                      <Input
                        type="text"
                        className="form-control"
                        placeholder="Apellido"
                        name="apellido"
                        onChange={e => {
                          this.handleChange(e);
                        }}
                      />
                    </InputGroup>
                    <InputGroup className="my-3">
                      <InputGroupAddon addonType="prepend">T</InputGroupAddon>
                      <Input
                        type="number"
                        pattern="[0-9]*"
                        inputMode="numeric"
                        className="form-control"
                        placeholder="Telefono"
                        name="telefono"
                        onChange={e => {
                          this.handleChange(e);
                        }}
                      />
                    </InputGroup>
                    <InputGroup className="my-3">
                      <InputGroupAddon addonType="prepend">@</InputGroupAddon>
                      <Input
                        type="email"
                        className="form-control"
                        name="correo"
                        onChange={e => {
                          this.handleChange(e);
                        }}
                        placeholder="correo"
                      />
                    </InputGroup>
                    <InputGroup className="my-3">
                      <InputGroupAddon addonType="prepend">*</InputGroupAddon>
                      <Input
                        name="psw"
                        className="form-control"
                        onChange={e => {
                          this.handleChange(e);
                        }}
                        placeholder="Password"
                      />
                    </InputGroup>
                    <InputGroup className="my-3">
                      <InputGroupAddon addonType="prepend">*</InputGroupAddon>
                      <Input
                        name="psw"
                        className="form-control"
                        onChange={e => {
                          this.handleChange(e);
                        }}
                        placeholder="Password"
                      />
                    </InputGroup>
                    <Button block color="success">
                      {" "}
                      Registrarme!
                    </Button>
                  </Form>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </header>
    );
  }
}
